                  </div>
                  <div class="indicator__composite">
                    <div class="indicator__bg"></div>
                    <div class="indicator__progress" style="width:20%"></div>
                  </div>
                  <div class="indicator__progress-value">
                    20%
                  </div>
                </div>
              </div>
